/**
 * Node.js file system adapter for the privacy-redaction package.
 *
 * Implements the FileSystemAdapter interface using Node.js fs APIs.
 * This enables the privacy package to read files, check existence, and
 * list directories in the Claude Code CLI plugin context.
 */

import { readFile, readdir, stat } from "node:fs/promises";
import type { FileSystemAdapter } from "@zest/privacy-redaction";

/**
 * Create a Node.js file system adapter for the privacy package.
 *
 * @param workspaceRoot - The root directory of the current workspace/project
 * @returns FileSystemAdapter implementation using Node.js fs APIs
 */
export function createNodeFsAdapter(workspaceRoot?: string): FileSystemAdapter {
  return {
    /**
     * Read file contents as UTF-8 string
     */
    async readFile(path: string): Promise<string> {
      return readFile(path, "utf-8");
    },

    /**
     * Check if a file exists at the given path
     */
    async fileExists(path: string): Promise<boolean> {
      try {
        await stat(path);
        return true;
      } catch {
        return false;
      }
    },

    /**
     * List files and directories in a directory
     */
    async readDir(path: string): Promise<string[]> {
      return readdir(path);
    },

    /**
     * Get the workspace root directory
     */
    getWorkspaceRoot(): string | undefined {
      return workspaceRoot;
    },
  };
}
